<template>
  <!-- <section> -->

  <el-container>
    <el-main>

      <c-el-table stripe
        :border="false"
        tree
        id="a-table"
        :data="group"
        highlight-current-row
        @selection-change="selsChange"
        height="100%"
        style="width: 100%;">
        <c-el-table-column label="名称"
          min-width="120"
          prop="authName"
          tree
          show-overflow-tooltip>
        </c-el-table-column>
        <c-el-table-column label="代码"
          min-width="120"
          prop="authCode"
          show-overflow-tooltip>
        </c-el-table-column>
        <c-el-table-column label="创建时间"
          min-width="150"
          prop="createDate"
          show-overflow-tooltip
          :formatter="dateFormatter">
        </c-el-table-column>
        <c-el-table-column label="描述"
          min-width="160"
          prop="description"
          show-overflow-tooltip>
        </c-el-table-column>
        <!--   <c-el-table-column label="操作" width="200">
        <template slot-scope="scope">
          <el-button size="small" @click="handleEditItem(scope.$index, scope.row)">编辑项</el-button>
          <el-button type="danger" size="small" @click="handleItemDel(scope.$index, scope.row)">删除项</el-button>
        </template>
      </c-el-table-column> -->
      </c-el-table>

      <!--编辑权限组界面-->
      <el-dialog title="编辑权限组"
        :visible.sync="editFormVisible"
        :close-on-click-modal="false">
        <el-form :model="editForm"
          label-width="80px"
          ref="editForm">
          <el-form-item label="名称"
            prop="authName"
            :rules="[{required:true,message:'请输入权限组名称',trigger:'blur'}]">
            <el-input v-model="editForm.authName"
              auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="代码"
            prop="authCode"
            :rules="[{required:true,message:'请输入权限组代码',trigger:'blur'}]">
            <el-input v-model="editForm.authCode"></el-input>
          </el-form-item>
          <!--
        <el-form-item label="请求地址" prop="url"  :rules="[{required:true,message:'请输入请求地址',trigger:'blur'}]"  >
          <el-input v-model="editForm.url" auto-complete="off"></el-input>
        </el-form-item>
        -->
          <el-form-item label="描述"
            prop="description">
            <el-input type="textarea"
              :autosize="{ minRows: 4, maxRows: 10}"
              placeholder="请添加相关描述"
              v-model="editForm.description"
              auto-complete="off"></el-input>
          </el-form-item>

        </el-form>
        <div slot="footer"
          class="dialog-footer">
          <el-button @click.native="editFormVisible = false">取消</el-button>
          <el-button type="primary"
            @click.native="editSubmit"
            :loading="editLoading">提交</el-button>
        </div>
      </el-dialog>

      <!--编辑权限项界面-->
      <el-dialog title="编辑权限项"
        :visible.sync="editItemVisible"
        :close-on-click-modal="false">
        <el-form :model="editItem"
          label-width="80px"
          ref="editItem">
          <el-form-item label="名称"
            prop="authName"
            :rules="[{required:true,message:'请输入权限项名称',trigger:'blur'}]">
            <el-input v-model="editItem.authName"
              auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="代码"
            prop="authCode"
            :rules="[{required:true,message:'请输入权限项代码',trigger:'blur'}]">
            <el-input v-model="editItem.authCode"></el-input>
          </el-form-item>
          <!--
         <el-form-item label="请求地址" prop="url" :rules="[{required:true,message:'请输入请求地址',trigger:'blur'}]"  >
          <el-input v-model="editItem.url" ></el-input>
        </el-form-item>
        -->
          <el-form-item label="描述"
            prop="description">
            <el-input v-model="editItem.description"
              type="textarea"
              :autosize="{ minRows: 4, maxRows: 10}"
              placeholder="请添加相关描述"
              auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer"
          class="dialog-footer">
          <el-button @click.native="editItemVisible = false">取消</el-button>
          <el-button type="primary"
            @click.native="editItemSubmit"
            :loading="editItemLoading">提交</el-button>
        </div>
      </el-dialog>

      <!--新建权限组界面-->
      <el-dialog title="新建权限组"
        :visible.sync="addFormVisible"
        :close-on-click-modal="false">
        <el-form :model="addForm"
          label-width="80px"
          ref="addForm">
          <el-form-item label="名称"
            prop="authName"
            :rules="[{required:true,message:'请输入权限组名称',trigger:'blur'}]">
            <el-input v-model="addForm.authName"
              auto-complete="off"></el-input>
          </el-form-item>

          <el-form-item label="代码"
            prop="authCode"
            :rules="[{required:true,message:'请输入权限组名称',trigger:'blur'}]">
            <el-input v-model="addForm.authCode"
              auto-complete="off"></el-input>
          </el-form-item>
          <!--
        <el-form-item label="请求地址" prop="url"  :rules="[{required:true,message:'请输入请求地址',trigger:'blur'}]"  >
          <el-input v-model="addForm.url" auto-complete="off"></el-input>
        </el-form-item>
      -->
          <el-form-item label="描述"
            prop="description">
            <el-input v-model="addForm.description"
              type="textarea"
              :autosize="{ minRows: 4, maxRows: 10}"
              placeholder="请添加相关描述"
              auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer"
          class="dialog-footer">
          <el-button @click.native="addFormVisible = false">取消</el-button>
          <el-button type="primary"
            @click.native="addSubmit"
            :loading="addLoading">提交</el-button>
        </div>
      </el-dialog>
      <!--新增权限项界面-->
      <el-dialog title="新增权限项"
        :visible.sync="addItemVisible"
        :close-on-click-modal="false">
        <el-form :model="addItem"
          label-width="80px"
          ref="addItem">
          <el-form-item label="名称"
            prop="authName"
            :rules="[{required:true,message:'请输入权限项名称',trigger:'blur'}]">
            <el-input v-model="addItem.authName"
              auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="代码"
            prop="authCode"
            :rules="[{required:true,message:'请输入权限项代码',trigger:'blur'}]">
            <el-input v-model="addItem.authCode"></el-input>
          </el-form-item>
          <!--
         <el-form-item label="请求地址" prop="url" :rules="[{required:true,message:'请输入请求地址',trigger:'blur'}]"  >
          <el-input v-model="addItem.url" ></el-input>
        </el-form-item>
        -->
          <el-form-item label="描述"
            prop="description">
            <el-input v-model="addItem.description"
              type="textarea"
              :autosize="{ minRows: 4, maxRows: 10}"
              placeholder="请添加相关描述"
              auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer"
          class="dialog-footer">
          <el-button @click.native="addItemVisible = false">取消</el-button>
          <el-button type="primary"
            @click.native="addItemSubmit"
            :loading="addItemLoading">提交</el-button>
        </div>
      </el-dialog>
    </el-main>
  </el-container>
  <!-- </section> -->
</template>

<script>
// import NProgress from 'nprogress'
export default {
  data() {
    return {
      addItem: {
        authName: "",
        authCode: "",
        url: "",
        description: ""
      },
      editItem: {
        authName: "",
        authCode: "",
        url: "",
        description: ""
      },

      abc: {
        a: true,
        b: true
      },
      group: [],

      listLoading: false,
      sels: [], // 列表选中列

      editFormVisible: false, // 编辑界面是否显示
      editLoading: false,
      addItemLoading: false,
      addItemVisible: false,

      dialogVisible: false,

      editItemLoading: false,
      editItemVisible: false,

      // 编辑界面数据
      editForm: {
        //     id: 0,
        authName: "",
        ahtuCode: "",
        description: ""
      },

      addFormVisible: false, // 新增界面是否显示
      addLoading: false,

      // 新增界面数据
      addForm: {
        authName: "",
        authCode: "",
        description: ""
      }
    };
  },
  methods: {
    permissonJudge() {
      const _this = this;
      _this.$http
        .post("/api/user/if")
        .then(function (response) {
          _this.abc.a = response.data.flag;
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    // 获取权限组列表
    getGroups() {
      const _this = this;
      _this.$http
        .post("/api/auth/list")
        .then(function (response) {
          _this.group = response.data.group;
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    // 删除
    handleDel: function () {
      this.$confirm("确认删除该权限组吗?", "提示", {
        type: "warning",
        closeOnClickModal: false
      })
        .then(() => {
          this.listLoading = true;
          // NProgress.start();
          const a = this.sels;
          const b = a.length;
          console.log(b);
          const para = { id: a[b - 1].authId };
          this.$http({
            url: "/api/auth/delete",
            method: "post",
            data: para
          }).then(res => {
            this.listLoading = false;
            // NProgress.done();
            if (res.data.flag) {
              this.$message({
                message: "删除成功",
                type: "success"
              });
            } else {
              this.$message({
                message: "删除失败",
                type: "error"
              });
            }
            this.getGroups();
          });
        })
        .catch(() => { });
    },
    handleItemDel: function (index, row) {
      this.$confirm("确认删除该权限项吗?", "提示", {
        type: "warning",
        closeOnClickModal: false
      })
        .then(() => {
          this.listLoading = true;
          // NProgress.start();
          const para = { id: row.authId };
          this.$http({
            url: "/api/auth/delete",
            method: "post",
            data: para
          }).then(res => {
            this.listLoading = false;
            // NProgress.done();
            if (res.data.flag) {
              this.$message({
                message: "删除成功",
                type: "success"
              });
            } else {
              this.$message({
                message: "删除失败",
                type: "error"
              });
            }

            this.getGroups();
          });
        })
        .catch(() => { });
    },
    // 显示编辑界面
    handleEdit: function () {
      this.editFormVisible = true;
      const a = this.sels;
      const b = a.length;

      this.editForm = Object.assign({}, this.sels[this.sels.length - 1]);
      console.log(this.sels);
      // console.log(this.editForm);
    },
    handleAddItem: function (index, row) {
      this.addItemVisible = true;
      this.addItem = {};
      this.addItem.parentId = row.authId;
      const a = row.authId;
      console.log(a);
    },
    handleEditItem: function (index, row) {
      console.log(this.editItem);
      console.log(this.editItemVisible);
      this.editItemVisible = true;
      this.editItem = Object.assign({}, row);
    },

    // 显示新增界面
    handleAdd: function () {
      this.addFormVisible = true;
      this.addForm = {
        name: "",
        description: ""
      };
    },
    // 编辑
    editSubmit: function () {
      this.$refs.editForm.validate(valid => {
        if (valid) {
          this.editLoading = true;
          // NProgress.start();
          const para = Object.assign({}, this.editForm);

          this.$http({
            url: "/api/auth/editAuth",
            method: "post",
            data: para
          }).then(res => {
            this.editLoading = false;
            // NProgress.done();
            if (res.data.flag) {
              this.$message({
                message: "编辑成功",
                type: "success"
              });
            } else {
              this.$message({
                message: "编辑失败",
                type: "error"
              });
            }

            this.$refs.editForm.resetFields();
            this.editFormVisible = false;
            this.getGroups();
          });
        }
      });
    },
    // 新增
    addSubmit: function () {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          this.addLoading = true;
          // NProgress.start();
          const para = Object.assign({}, this.addForm);

          this.$http({
            method: "post",
            // url:'api/permission/addGroup',
            url: "api/auth/addAuth",
            data: para
          }).then(res => {
            this.addLoading = false;
            // NProgress.done();
            if (res.data.flag) {
              this.$message({
                message: res.data.msg,
                type: "success"
              });
            } else {
              this.$message({
                message: res.data.msg,
                type: "error"
              });
            }
            this.$refs.addForm.resetFields();
            this.addFormVisible = false;
            this.getGroups();
          });
        }
      });
    },
    addItemSubmit: function () {
      this.$refs.addItem.validate(valid => {
        if (valid) {
          this.addItemLoading = true;
          // NProgress.start();
          const para = Object.assign({}, this.addItem);

          this.$http({
            method: "post",
            url: "api/auth/addAuth",
            data: para
          }).then(res => {
            this.addItemLoading = false;
            // NProgress.done();
            if (res.data.flag) {
              this.$message({
                message: res.data.msg,
                type: "success"
              });
            } else {
              this.$message({
                message: res.data.msg,
                type: "error"
              });
            }
            this.$refs.addItem.resetFields();
            this.addItemVisible = false;
            this.getGroups();
          });
        }
      });
    },
    editItemSubmit: function () {
      this.$refs.editItem.validate(valid => {
        if (valid) {
          this.editItemLoading = true;
          // NProgress.start();

          const para = Object.assign({}, this.editItem);

          this.$http({
            url: "/api/auth/editAuth",
            method: "post",
            data: para
          }).then(res => {
            this.editItemLoading = false;
            // NProgress.done();
            if (res.data.flag) {
              this.$message({
                message: "编辑成功",
                type: "success"
              });
            } else {
              this.$message({
                message: "编辑失败",
                type: "error"
              });
            }

            this.$refs.editItem.resetFields();
            this.editItemVisible = false;
            this.getGroups();
          });
        }
      });
    },
    selsChange: function (sels) {
      this.sels = sels;
      console.log(sels);
    },
    dateFormatter: function (row, column) {
      if (row.createDate != null) {
        return moment(row.createDate).format("YYYY-MM-DD ");
      }
    }
  },
  mounted() {
    this.getGroups();
    //   this.changeDate();
  }
};
</script>


<style>
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 120px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 20%;
}
</style>
